@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{font-family:'Roboto', sans-serif;color:#373d46;margin:0;padding:0}.container{padding:0}#profile-image{width:100%;height:100%}img{display:block;position:fixed;width:100%;z-index:0;background-color:#1b1b1b}#profile{position:fixed;width:100vw;height:40vh;bottom:0;padding:15px;background-color:whitesmoke;box-shadow:0px -5px 100px 0px rgba(0,0,0,0.9)}#profile-name #name{font-weight:700;font-size:36px;padding-right:10px}#profile-name #radius{font-weight:400;font-size:28px}#profile-location #location{padding-left:10px}#profile-description{margin-top:5px}#profile-long-description{height:100px}#profile-actions{text-align:center}#profile-actions svg{height:60px;width:60px;padding:13px;margin-left:15px;margin-right:15px;border-radius:40px;box-shadow:0px 0px 10px 0px rgba(0,0,0,0.1);transition:0.4s}#profile-actions svg.fa-times{color:#d63031}#profile-actions svg.fa-star{color:#0984e3}#profile-actions svg.fa-heart{color:#00b894}#profile-actions svg:active{background-color:#c7c8ca}

