@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;900&display=swap');

body {
  font-family: 'Roboto', sans-serif;
  color: rgb(55, 61, 70);
  margin: 0;
  padding: 0;
}

.container {
  padding: 0;
}

#profile-image {
  width: 100%;
  height: 100%;
}

img {
  display: block;
  position: fixed;
  width: 100%;
  z-index: 0;
  background-color: rgb(27, 27, 27)
}

#profile {
  position: fixed;
  width: 100vw;
  height: 40vh;
  bottom: 0;
  padding: 15px;
  background-color: whitesmoke;
  -webkit-box-shadow: 0px -5px 100px 0px rgba(0,0,0,0.9);
  -moz-box-shadow: 0px -5px 100px 0px rgba(0,0,0,0.9);
  box-shadow: 0px -5px 100px 0px rgba(0,0,0,0.9);
}

#profile-name #name {
  font-weight: 700;
  font-size: 36px;
  padding-right: 10px;
}

#profile-name #radius {
  font-weight: 400;
  font-size: 28px;
}

#profile-location #location {
  padding-left: 10px;
}

#profile-description {
  margin-top: 5px;
}

#profile-long-description {
  height: 100px;
}

#profile-actions {
  text-align: center;
}

#profile-actions svg {
  height: 60px;
  width: 60px;
  padding: 13px;
  margin-left: 15px;
  margin-right: 15px;
  border-radius: 40px;
  -webkit-box-shadow: 0px 0px 10px 0px rgba(0,0,0,0.1);
  -moz-box-shadow: 0px 0px 10px 0px rgba(0,0,0,0.1);
  box-shadow: 0px 0px 10px 0px rgba(0,0,0,0.1);
  transition: 0.4s;
}

#profile-actions svg.fa-times {
  color: rgb(214, 48, 49);
}

#profile-actions svg.fa-star {
  color: rgb(9, 132, 227);
}

#profile-actions svg.fa-heart {
  color: rgb(0, 184, 148);
}

#profile-actions svg:active {
  background-color: #c7c8ca;
}